<template>
  <div id="app">
<!--     <div class='ribbon'>
       <router-link to="/" exact><span>首页</span></router-link>
       <router-link to="/topics/11"><span>异步数据</span></router-link>
       <router-link to="/counter"><span>Vue原生事件</span></router-link>
       <router-link to="/about"><span>无状态组件</span></router-link>
       <router-link to="/test"><span>异步组件</span></router-link>
     </div> -->
     <img width="300px" src="./assets/logo.png">
     <router-view class="view">></router-view>
  </div>
</template>

<script>
export default {
  name: "app"
};
</script>

<style>
* {
  /* Basic CSS reset */
  margin: 0;
  padding: 0;
}
body {
  background: #eee;
  background-image: url(./assets/furley_bg.png);
  text-align: center;
}
.ribbon {
  display: inline-block;
}

.ribbon:after,
.ribbon:before {
  margin-top: 0.5em;
  content: "";
  float: left;
  border: 1.5em solid #fff;
}

.ribbon:after {
  border-right-color: transparent;
}

.ribbon:before {
  border-left-color: transparent;
}

.ribbon a:link,
.ribbon a:visited {
  color: #000;
  text-decoration: none;
  float: left;
  height: 3.5em;
  overflow: hidden;
}

.ribbon span {
  background: #fff;
  display: inline-block;
  line-height: 3em;
  padding: 0 1em;
  margin-top: 0.5em;
  position: relative;

  -webkit-transition: background-color 0.2s, margin-top 0.2s; /* Saf3.2+, Chrome */
  -moz-transition: background-color 0.2s, margin-top 0.2s; /* FF4+ */
  -ms-transition: background-color 0.2s, margin-top 0.2s; /* IE10 */
  -o-transition: background-color 0.2s, margin-top 0.2s; /* Opera 10.5+ */
  transition: background-color 0.2s, margin-top 0.2s;
}

.ribbon a:hover span {
  background: #ffd204;
  margin-top: 0;
}

.ribbon span:before {
  content: "";
  position: absolute;
  top: 3em;
  left: 0;
  border-right: 0.5em solid #9b8651;
  border-bottom: 0.5em solid #fff;
}

.ribbon span:after {
  content: "";
  position: absolute;
  top: 3em;
  right: 0;
  border-left: 0.5em solid #9b8651;
  border-bottom: 0.5em solid #fff;
}
</style>



